*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-color-text: #333;
  --secondary-color-text: #fff;
  --logo-color: #28a745;
  --section-about-background: #f4f4f4;
  --green-background: #28a745;
  --blue-background: #0284d0;
  --phone-color-text: gray;
  --hero-color-text: #e9e9e9;
  --time-function: cubic-bezier(.4, 0, .2, 1);
  --button-activ-color: #0284d0;
}

body {
  background-color: var(--secondary-color-text);
  color: var(--main-color-text);
  font-family: Open Sans, sans-serif;
  font-size: 18px;
  line-height: 1.56;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.button, .callback-btn, .blog-btn, .hero-button {
  display: inline-block;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

time {
  display: inline-block;
}

.button, .callback-btn, .blog-btn, .hero-button {
  height: 50px;
  color: var(--secondary-color-text);
  cursor: pointer;
  border: rgba(0, 0, 0, 0);
  border-radius: 5px;
  font-family: inherit;
}

.container {
  width: 1360px;
  margin: 0 auto;
  padding-left: 28px;
  padding-right: 28px;
}

.info-description {
  color: var(--secondary-color-text);
  font-size: 20px;
  line-height: 1.65;
}

.info-title {
  color: var(--secondary-color-text);
  font-size: 38px;
  font-weight: 300;
  line-height: 1.27;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.section-top-title {
  font-size: 20px;
  line-height: 1.65;
}

.section-top-title.light {
  color: var(--secondary-color-text);
  margin-bottom: 5px;
}

.section-top-title.dark {
  color: var(--primary-color-text);
  text-align: center;
  margin-bottom: 16px;
}

.section-title {
  font-size: 38px;
  font-weight: 300;
  line-height: 1.26;
}

.section-title.light {
  color: var(--secondary-color-text);
  margin-bottom: 26px;
}

.section-title.dark {
  color: var(--primary-color-text);
  text-align: center;
  margin-bottom: 34px;
}

.section-description {
  line-height: 1.56;
}

.section-description.light {
  color: var(--secondary-color-text);
  margin-bottom: 17px;
}

.section-description.dark {
  color: var(--primary-color-text);
  text-align: center;
  margin-bottom: 39px;
}

.up-button {
  background-color: var(--button-activ-color);
  cursor: pointer;
  width: 46px;
  height: 46px;
  transition: scale 250ms var(--time-function);
  transition: background-color 250ms var(--time-function);
  border-radius: 10%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  bottom: 80px;
  right: 6px;
}

.up-button__icon {
  content: "";
  width: 40px;
  height: 40px;
  fill: var(--secondary-color-text);
  background: center / 100% 100% no-repeat;
}

.up-button:hover {
  filter: drop-shadow(0 5px 5px navy);
  background-color: #00008b;
  scale: 1.1;
}

.up-button_hiden {
  display: none;
}

@media screen and (max-width: 830px) {
  .up-button {
    width: 20px;
    height: 40px;
  }

  .up-button__icon {
    width: 30px;
    height: 30px;
  }
}

@media screen and (max-width: 767px) {
  .up-button {
    width: 30px;
    height: 30px;
  }

  .up-button__icon {
    width: 25px;
    height: 25px;
  }
}

@media screen and (max-width: 537px) {
  .up-button {
    width: 17px;
    height: 31px;
    border-radius: 22%;
    bottom: 140px;
  }

  .up-button__icon {
    width: 15px;
    height: 15px;
  }
}

